/*Pages global*/
.apps-page,
.home-page,
.about-page {
  z-index: 0;
}
.apps-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.coding-wallpaper-container {
  width: 100%;
  position: absolute;
  top: 0rem;
  left: 0rem;
  height: 200vh;
  max-height: 100%;
  /* border: 1px solid red; */
  overflow: hidden;
}
.coding-wallpaper {
  z-index: 0;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100vw;
  filter:grayscale(1) invert() blur(2px);
  opacity: 0.4;
  object-fit: cover;
  
}
[data-theme='dark'] .coding-wallpaper {
  filter: grayscale(0%) invert(0) blur(0px);
}

/*Home page*/
.home-page-intro {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  border-radius: 8px;
  padding: 0px 12px 48px 12px;
  margin-top: 5rem;
}

.home-page-intro h3 {
  /* color: var(--text-heading); */
  font-size: 2.4rem;
  margin-bottom: 16px;
  margin-top: 16px;
  letter-spacing: -1.4px;
  color: var(--text-heading);
}
.home-page-intro article {
  color: var(--text-body);
  font-size: 1.1rem;
  letter-spacing: 0.8px;
  opacity: 0.8;
  width: 75%;
}

.home-container {
  position: relative;
  display: grid;
  grid-template-areas: 
    'two two three'
    'one one one'
    ;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 24px;
  margin-inline: auto;
  align-items: center;
  justify-content: center;
  width: 80%;
  margin-bottom: 48px;
}
.preview-card {
  color: var(--text-body);
  background: linear-gradient(135deg, var(--bg-main), var(--bg-main-two));
  min-height: 5rem;
  border-radius: 24px;
  padding: 12px 8px;
  font-size: 1.2rem;
  box-shadow: 0px 0px 12px 1px rgba(var(--text-body-rgb), 0.4);
}
.preview-apps {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  padding: 12px 28px;
  grid-area: one;
}
.preview-apps-div {
  width: fit-content;
  display: grid;
  gap: 0.5rem;
  grid-template-areas: 
    'app1 app2 app3'
    'app4 app4 app3'
    'app4 app4 app3'
  ;
  align-items: center;
}
.preview-app-image {
  width: 8rem;
  border-radius: 0.75rem;
  filter: brightness(0.8);
  transition: all 350ms;
  cursor: pointer;
}
.preview-app-image:hover {
  scale: 1.2;
  filter: brightness(1.05);
  z-index: 2;
}
.preview-apps .hangman {
  grid-area: app3;
}
.preview-apps .image-slider {
  grid-area: app1
}
.preview-apps .movie-library {
  width: 16rem;
  grid-area: app4;
}
.preview-apps .recipe {
  grid-area: app2;
}
.preview-apps-description {
  font-size: 1.4rem;
  letter-spacing: 1px;
  width: 50%;
}
.preview-about {
  grid-area: two;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 1rem 2rem;
}
.about-me-picture-div {
  
}
.about-me-picture {
  width: 8rem;
  border-radius: 50%;
}
.about-me-description {
  margin-left: 3rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.location-pin {
  background: linear-gradient(180deg, var(--text-body), var(--text-body) 60%, rgba(var(--text-body-rgb), 0));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.2rem;
  transform: translate(0px, 3px);
  margin-right: 0.5rem;
}
.work-pin {
  font-size: 1.2rem;
  transform: translate(0px, 3px);
  margin-right: 0.5rem;
}
.school-pin {
  font-size: 1.2rem;
  transform: translate(0px, 3px);
  margin-right: 0.5rem;
}
.about-me-description h3 {
  font-size: 2rem;
  letter-spacing: -0.5px;
  margin: 0;
  color: var(--text-heading);
}
.about-me-description p {
  display: inline;
  letter-spacing: 1px;
  font-size: 0.8rem;
  margin: 0;
  margin-bottom: 0.4rem;
}
.preview-contact {
  grid-area: three;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 1.5rem 2rem;
}
.preview-contact h3 {
  font-size: 1.6rem;
  letter-spacing: -1px;
  margin: 0;
  margin-bottom: 0.75rem;
  color: var(--text-heading);
}
.preview-contact p {
  font-size: 1rem;
  align-self: flex-start;
  margin: 0;
  margin-bottom: 1rem;
}
.preview-contact a {
  color: var(--text-body);
}

/*Apps page*/

/*About page*/
.about-me-container {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  padding: 2rem;
}
.about-intro {
  margin-bottom: 0;
  padding-bottom: 12px;
}
.about-me-hero {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 80%;
  min-height: 200px;
  color: var(--text-body);
  background: linear-gradient(135deg, var(--bg-main), var(--bg-main-two));
  border-radius: 24px;
  box-shadow: 0px 0px 24px 1px rgba(var(--text-body-rgb), 0.4);
  padding: 16px 48px;
  margin-bottom: 48px;
}
.about-me-hero h3 {
  /* color: var(--text-heading); */
  font-size: 2.4rem;
  margin-bottom: 16px;
  margin-top: 16px;
  letter-spacing: -1.4px;
  color: var(--text-heading);
}
.about-me-hero article {
  color: var(--text-body);
  font-size: 1rem;
  width: 75%;
  text-align: left;
  line-height: 1.2rem;
  padding-left: 2rem;
  span {
    font-size: 1.05rem;
    font-weight: 600;
    color: var(--text-heading);
  };
}
[glow] .about-me-article {
  color: orange;
}
.about-me-hexagon-div {
  
}
.about-me-hexagon {
  scale: 10;
  position: absolute;
  top: 6.5rem;
  left: 6rem;
  color: var(--text-body);
  z-index: 1;
  transition: all 150ms;
}
.hexagon-profile-pic {
  position: absolute;
  top: 6.5rem;
  left: 6rem;
  width: 1rem;
  mask-image: url(#icon-mask);
  -webkit-mask-image: url(#icon-mask);
  object-fit: cover;
  scale: 9.6;
  filter: grayscale(0.8) brightness(0.9);
  transition: all 350ms;
}
.its-me-text {
  color: var(--text-heading);
  position: absolute;
  top: 11rem;
  left: 5rem;
  opacity: 0;
  scale: 1;
  transition: all 350ms ease-in-out;
}
.about-me-hexagon-div:hover .hexagon-profile-pic {
  filter: grayscale(0) brightness(1);
}
.about-me-hexagon-div:hover .its-me-text {
  top: 8.5rem;
  opacity: 1;
  scale: 1.2;
}
.about-me-hexagon:hover {
  filter: brightness(1.2);
  text-shadow: 2px 2px 1px red;
}
.about-profile-pic {
  width: 8rem;
  border-radius: 50%;
}
.glow-capture {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.glow-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 2;
}
