.movie-library-preview-div {
  max-width: 40vw;
  margin-top: 2rem;
  border-radius: 1rem;
  object-fit: cover;
  overflow: hidden;
}
.movie-library-preview-image {
  max-width: 40vw;
  transition: all 350ms;
  filter: brightness(0.9) blur(0.25px);
}
.movie-library-preview-image:hover {
  scale: 1.1;
  filter: brightness(1.1) blur(0);
  transform: translate(0px, 1rem);
}
